import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { SharedService } from 'src/app/services/shared.service';

@Injectable({
  providedIn: 'root',
})
export class AllocationService {
  apiUrl = this.sharedService.getUrl;
  despositeAmount = new BehaviorSubject(undefined);
  _viewDepositAmount = new BehaviorSubject(undefined);
  _mainFundAlocationOrderItemDetails = new BehaviorSubject(undefined);
  constructor(private http: HttpClient, public sharedService: SharedService) { }



  /**
   * get last allocation orders
   */

  allocationOrdersService(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AllocationOrderDepositCash`, params,
      this.sharedService.getHeaders()
    );
  }
  getRegisterDirectDeposits(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetRegisterDirectDeposits${params}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterUnsettledCheques(param): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetRegisterUnsettledCheques${param}`,
      this.sharedService.getHeaders()
    );
  }
  addRegisterDirectCashDepositToMarkaziaAccount(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AddRegisterDirectCashDepositToMarkaziaAccount`, params,
      this.sharedService.getHeaders()
    );
  }
  addRegisterDirectChequesToMarkaziaAccount(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AddRegisterDirectChequesToMarkaziaAccount`, params,
      this.sharedService.getHeaders()
    );
  }
  addCashDepositToMarkaziaAccount(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AddCashDepositToMarkaziaAccount`, params,
      this.sharedService.getHeaders()
    );
  }
  addChequesToMarkaziaAccount(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AddChequesToMarkaziaAccount`, params,
      this.sharedService.getHeaders()
    );
  }
  allocationOrderDepositCheque(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/AllocationOrderDepositCheque`, params,
      this.sharedService.getHeaders()
    );
  }
  getCashDepositDetails(depositId): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetCashDepositDetails?depositeId=${depositId}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterDirectCashDepositDetails(depositId): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetRegisterDirectCashDepositDetails?depositeId=${depositId}`,
      this.sharedService.getHeaders()
    );
  }
  getMainFundCash(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Mainfund/GetMainFundCash`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterDirectChequeDepositDetails(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetRegisterDirectChequeDepositDetails${params}`,
      this.sharedService.getHeaders()
    );
  }
  getChequeDepositDetails(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetChequeDepositDetails${params}`,
      this.sharedService.getHeaders()
    );
  }
  getDepositsById(id,type): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetDeposits?DepositeId=${id}&DepositeType=${type}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterDirectDepositsNew(id, type): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetRegisterDirectDeposits?DepositeId=${id}&DepositeType=${type}`,
      this.sharedService.getHeaders()
    );
  }
  getCurrencyList(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Lookups/GetLookups?lookupTypeId=9`,
      this.sharedService.getHeaders()
    );
  }
  getMainFundSettledCheques(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetMainFundSettledCheques${params}`,

      this.sharedService.getHeaders()
    );
  }
  getMainFundSettledChequesNew(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetMainFundSettledChequesNew${params}`,

      this.sharedService.getHeaders()
    );
  }
  getLastAllocation(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetLastAllocationOrder`,

      this.sharedService.getHeaders()
    );
  }
  /**
   * get list of allocation order
   */
  getAllocation(pageNo?: number, search?: string, sort?: number): Observable<any> {
    let queryString = `AllocationOrdersService/GetAllocationOrders?PageNo=${pageNo}&PageSize=6&sort=${sort}`;

    if (search) {
      queryString = queryString + `&search=${search}`
    }
    return this.http.get<any>(
      this.apiUrl + queryString,
      this.sharedService.getHeaders()
    );
  }

  //veiw detail get allocation
  getViewDetailAllocation(orderId: number,): Observable<any> {
    return this.http.get<any>(
      this.apiUrl +
      `AllocationOrdersService/GetAllocationOrderDetails?OrderId=${orderId}`,

      this.sharedService.getHeaders()
    );
  }
  getListOfSettledChequesForAllocationOrderDetails(params): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/ListOfSettledChequesForAllocationOrderDetails${params}`,

      this.sharedService.getHeaders()
    );
  }
  viewCashAllocationOrderDetailsDeposits(orderId): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/ViewCashAllocationOrderDetailsDeposits?allocationOrderDetailsId=${orderId}`,

      this.sharedService.getHeaders()
    );
  }
  viewChequeAllocationOrderDetailsDeposits(orderId): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/ViewChequeAllocationOrderDetailsDeposits?allocationOrderDetailsId=${orderId}`,

      this.sharedService.getHeaders()
    );
  }
}
